@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Titillium Web", sans-serif;
}

:root {
  --mainColor: #f5f6f0;
  --mainColorLight: #5767aa;
  --secondaryColor: #c9a6a9;
  --textColor: #1a1818;
}
.sym {
  font-size: 25px;
  transition: 0.3s;
}
.sym:hover{
  transition: 0.3s;
  font-size: 26px;
}

.icons{
  color: gray;
}
.name {
  color: brown;
  align-items: baseline;
}

header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 80px;
  padding: 0 2rem;
  background-color: var(--mainColor);
  color: var(--textColor);
}

nav a {
  margin: 0 1rem;
  color: var(--textColor);
  text-decoration: none;
  transition: 0.3s;
}

.logo {
  align-items: baseline;
}

nav a:hover {
  color: var(--secondaryColor);
  font-size: 20px;
  transition: 0.3s;
}

header .navbtn {
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  outline: none;
  color: var(--textColor);
  visibility: hidden;
  opacity: 0;
  font-size: 1.8rem;
}

header div,
nav {
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 1024px) {
  header .navbtn {
    visibility: visible;
    opacity: 1;
  }

  header nav {
    position: fixed;
    top: -100vh;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1.5rem;
    background-color: var(--mainColor);
    transition: 1s;
